var html = require('choo/html')
var raw = require('choo/html/raw')
var Nanocomponent = require('nanocomponent')
var css = require('sheetify')
var fs = require('fs')

var g0v = fs.readFileSync('./app/articles/g0v.html', 'utf8')
var geach = fs.readFileSync('./app/articles/geach.html', 'utf8')

var titleObject = {
  g0v: 'Open Communities',
  geach: 'Geach on Meaning'
}

css('./article.scss')

class Component extends Nanocomponent {
  constructor () {
    super()
  }

  createElement (state, emit) {
    var article = state.params.article

    emit(state.events.DOMTITLECHANGE, titleObject[article])

    return html`
      <article class='boowa-article'>
        ${raw(eval(article))}
      </article>
    `
  }

  update () {
    return true
  }
}
module.exports = new Component()