"use strict";var _$articleList_5=function(state){return"\n  <li>\n    <h2>Projects</h2>\n    <ul>\n      <li><span><mark>Usable security</mark> at <a href='https://simplysecure.org/'>Simply Secure</a>, previously <mark>open source funding</mark> at <a href='https://prototypefund.de/'>Prototype Fund</a></span></li>\n      <li><span><mark>Analytical philosophy</mark> at Trinity College, Cambridge, <mark>mathematical logic</mark> at ILLC, Amsterdam</span></li>\n    </ul>\n  </li>\n  <li>\n    <h2>Ping me</h2>\n    <ul>\n      <li><span>Email: <mark>hello@bumble.blue</mark> (GPG key ID: 3A6B8452)</span></li>\n      <li><span>Twitter: <a href='https://twitter.com/bumblblu'>@bumblblu</a></span></li>\n      <li><span>Mastodon: <a href='https://chaos.social/@bumbleblue'>@bumbleblue@chaos.social</a></span></li>\n      <li><span>GitHub: <a href='https://github.com/bumbleblue'>@bumbleblue</a></span></li>\n    </ul>\n  </li>\n  <li>\n    <h2>Propaganda</h2>\n    <ul>\n      <li><span>Open resources: Simply Secure has a <a href='https://simplysecure.org/knowledge-base/''>knowledge base</a> for building UX design skills in privacy, security, and transparency.</span></li>\n      <li><span>Open-ended hierarchies: If plural logic is awesome, then superplural logic must be super awesome! More about cheerios, innocence and open-ended hierarchies in my master\u2019s thesis, <a href='https://www.illc.uva.nl/Research/Publications/Reports/MoL-2015-23.text.pdf'>Superplural Logic</a>.</span></li>\n      <li><span>Open access: Scientific data and ideas should be free and accessible to all. Here\u2019s Wittgenstein\u2019s <a href='http://www.wittgensteinsource.org'>Nachlass</a>, published and curated by the Wittgenstein Archives at the University of Bergen.</span></li>\n      <li><span>Open relationships: If you heard me talk about it, I most likely recommended Carrie &amp; Jonathan Ichikawa Jenkins\u2019s article <a href='https://www.dropbox.com/s/cgaw1sxphy1zev4/On%20Being%20The%20Only%20Ones.pdf?dl=0'>(PDF)</a> to you.</span></li>\n      <li><span>Open knowledge: The <a href='https://okfn.de'>Open Knowledge Foundation</a> develops digital tools to empower citizens. I gave a talk about the mission at <a href='https://youtu.be/lM0U0i2TNkQ'>TEDxHeidelberg</a>.</span></li>\n      <li><span>Open rehearsals: You like singing, close harmony, and sappy old love songs? Oh wow. I guess you should join your local barbershop community. Female Berliners, why not check out the <a href='http://womeninblack.de/'>Women in Black</a>.</span></li>\n      <li><span>Open table: I run a <a href='https://en.wikipedia.org/wiki/Stammtisch'>stammtisch</a> in Berlin. Join our <a href='https://lists.posteo.de/listinfo/schlammtisch'>mailing list</a> to attend!</span></li>\n    </ul>\n  </li>\n  <li>\n    <h2>Posts</h2>\n    <ul>\n      <li><span><a href='./#articles/g0v'>Open Communities</a> Mar 30, 2017</span></li>\n      <li><span><a href='./#articles/geach'>Geach on Meaning</a> Jan 20, 2014</span></li>\n    </ul>\n  </li>\n"};

